/* App.css */

:root {
  --color-background: white;

  --color-background-result: rgb(135, 78, 249);
  --color-background-result-shadow: rgba(92, 25, 226);
  --color-text-result: white;
  --color-summary-bold: rgb(0, 0, 64);

  --color-background-reaction:red;
  --color-background-memory: yellow;
  --color-background-verbal: green;
  --color-background-visual: blue;
}

html {
  font-size: 10px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
  font-family: 'Hanken Grotesk';
}

#result-summary {
  margin: 200px auto;

  width: 500px;
  height: 340px;
  border-radius: 20px;
  background-color: var(--color-background);

  box-shadow: 
  0.5px 0.5px 0px 0px rgb(131 214 255 / 16%), 
  12px 11px 20px 0px rgb(131 170 255 / 12%);

  display: flex;
}

.result-summary-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

  width: 50%;
  padding: 20px 5px;
}

/* result-column */

#result-column {
  background-color: var(--color-background-result);
  box-shadow: inset 0px -170px 140px 20px var(--color-background-result-shadow);
  border-radius: 30px;
  color: var(--color-text-result);
}

.section-title {
  font-size: 1.4rem;
  font-weight: 700;
}

.gray {
  color: rgba(255, 245, 245, 0.6);
}

#total-score {
  width: 55%;
  height: 43%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2px;
  margin-top: -10px;

  background-color: rgba(30, 30, 216, 0.886);
  box-shadow: inset 0px -50px 40px -20px rgba(146, 177, 255, 0.161);

  border-radius: 50%;
}

#result {
  font-size: 4.5rem;
  color: white;
  font-weight: 700;
}

#total-score p:last-child {
  font-size: 1.1rem;
}

#result-column h4 {
  font-size: 2.5rem;
  margin-top: -20px;
}

#result-comment {
  font-size: 1.2rem;
  width: 75%;
}

/* summary column */

#summary-column h5, .score-text {
  color: var(--color-summary-bold);
}

.categories {
  width: 75%;
  height: 10%;

  display: flex;
  justify-content: space-between;

  font-weight: 700;
}
